:root {
  --t: 90%;
  --l: 50%;
  --bl: 2px solid transparent;
  --br: 2px solid black;
}

body {
  margin: 0;
}

.tooltip {
  color: #fff;
  background-color: #7e7e7e;
  border: 2px solid #000;
  border-radius: 2px;
  padding: 10px;
  font-family: Tahoma, Arial, sans-serif;
  font-size: .9em;
  position: relative;
  transform: translateY(0);
}

.tooltip:after {
  content: "";
  top: var(--t);
  left: var(--l);
  z-index: -1;
  border-top: 2px solid #0000;
  border-bottom: 2px solid #000;
  border-left: var(--bl);
  border-right: var(--br);
  background-color: #7e7e7e;
  width: 25px;
  height: 25px;
  position: absolute;
  transform: rotateZ(45deg)translateX(-50%);
}
