:root {
    --t: 90%;
    --l: 50%;
    --bl: 2px solid transparent;
    --br: 2px solid black;
}

body {
    margin: 0
}

.tooltip {
    background-color: #7e7e7e;
    color: white;
    padding: 10px;
    position: relative;
    border: 2px solid black;
    border-radius: 2px;
    transform: translateY(0px);
    font-family: Tahoma, Arial, sans-serif;
    font-size: 0.9em;
}

.tooltip::after {
    position: absolute;
    content: '';
    width: 25px;
    height: 25px;
    background-color: #7e7e7e;
    top: var(--t);
    left: var(--l);
    transform: rotateZ(45deg) translateX(-50%);
    z-index: -1;
    border-bottom: 2px solid black;
    border-top: 2px solid transparent;
    border-left: var(--bl);
    border-right: var(--br);
}